
.clearfix:after {
  content: '';
  clear: both;
  display: block;
}
.contact {
  margin-left: 40px;
  margin-right: 40px;
}
.pic-wrapper {
  margin-top: 120px;
  width: 100%;
  padding-bottom: 30px;
  border-bottom: 1px solid #008000;
}
.pic-wrapper .pic {
  width: 50%;
  float: left;
  text-align: center;
}
.pic-wrapper .pic img {
  width: 50%;
}
.pic-wrapper .pic p {
  font-size: 30px;
}


