
#nav {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  width: 250px;
  background: linear-gradient(135deg, #2c2e2c 0, #202021 100%);
}
#nav .header {
  margin: 30px auto 30px auto;
  width: 50%;
  text-align: center;
}
#nav .header .header-img {
  width: 125px;
  height: 125px;
  border-radius: 50%;
  background: #fff;
}
#nav .header .header-img img {
  border-radius: 50%;
}
#nav .header .name {
  margin-top: 25px;
  font-size: 40px;
}
#nav .header .name a {
  color: #61c191;
}
#nav .nav-container {
  margin-top: 40px;
  margin-left: 60px;
}
#nav .nav-container li {
  margin-bottom: 50px;
}
#nav .nav-container li a {
  color: #fff;
  font-size: 20px;
  transition: all 0.3s;
}
#nav .nav-container li a.active {
  color: #f9c427;
}
#nav .nav-container li a:hover {
  color: #2bb0d4;
}
#nav #footer {
  position: absolute;
  bottom: 10px;
  left: 40px;
  right: 40px;
  text-align: center;
  font-size: 0;
}
#nav #footer .logo a {
  display: inline-block;
  margin: 5px;
  line-height: 40px;
  text-align: center;
  width: 40px;
  height: 40px;
  color: #fff;
  font-size: 24px;
  border-radius: 50%;
}
#nav #footer .logo .r-youxiang {
  background-color: #00ab6c;
}
#nav #footer .logo .r-shu {
  background-color: #584613;
}
#nav #footer .logo .r-github {
  color: #000;
  background-color: #fff;
}
#nav #footer .source {
  margin-top: 20px;
  font-size: 14px;
  color: #999;
}


