
.clearfix:after {
  content: '';
  clear: both;
  display: block;
}
.skill {
  background: #eee;
}
.skill h1 {
  padding: 30px;
  text-align: center;
  font-size: 40px;
  font-weight: 900;
}
.skill .pic {
  background: #fff;
  text-align: center;
}
.skill .desc {
  margin-left: 50px;
  margin-top: 30px;
  padding-bottom: 30px;
  background: #eee;
}
.skill .desc .title {
  width: 50%;
  float: left;
}
.skill .desc .title li {
  margin-top: 15px;
  list-style-type: circle !important;
  color: #585858;
}
.skill .desc .title li:first-child {
  margin-left: -20px;
  list-style: none !important;
}


