
.info {
  position: relative;
  background: url("https://oapy20uns.qnssl.com/baground.jpg");
  background-position: center;
  background-size: cover;
  color: #fff;
}
.info .info-bg {
  box-sizing: border-box;
  padding-top: 40px;
  padding-left: 60px;
  overflow: hidden;
  height: 100vh;
  background: rgba(0,0,0,0.5);
  text-overflow: ellipsis;
}
.info .info-bg .title {
  font-size: 28px;
  margin-bottom: 30px;
}
.info .info-bg .basic {
  margin-bottom: 40px;
}
.info .info-bg .basic li {
  margin-bottom: 10px;
  line-height: 30px;
  height: 30px;
  font-size: 18px;
}
.info .info-bg .desc {
  margin-right: 450px;
  line-height: 35px;
  font-size: 18px;
}
@media only screen and (max-width: 1280px) {
.info .info-bg .desc {
    line-height: 25px;
}
}


