
.clearfix:after {
  content: '';
  clear: both;
  display: block;
}
.work {
  width: 100%;
}
.work .name {
  margin: 30px;
  font-size: 32px;
  color: #000;
  font-weight: 900;
}
.work .project-wrapper {
  margin: 0 auto;
  width: 90%;
}
.work .project-wrapper:last-child {
  margin-bottom: 40px;
}
.work .project-wrapper a {
  position: relative;
  display: block;
  float: left;
  overflow: hidden;
  outline: 1px solid #fff;
  width: 50%;
  transition: all 0.3s;
  font-size: 0;
}
.work .project-wrapper a.item2:hover {
  background-color: #4ec141;
}
.work .project-wrapper a.item3:hover {
  background-color: #237dca;
}
.work .project-wrapper a.item4:hover {
  background-color: #d46b2b;
}
.work .project-wrapper a.item5:hover {
  background-color: #bf0404;
}
.work .project-wrapper a.item6:hover {
  background-color: #bfb31b;
}
.work .project-wrapper a:hover {
  background-color: #67d2bf;
}
.work .project-wrapper a:hover img {
  opacity: 0.2;
  transform: scale3d(1.05, 1.05, 1);
}
.work .project-wrapper a:hover .desc {
  background-color: transparent;
}
.work .project-wrapper a:hover .desc h2 {
  transform: translateY(-50px);
}
.work .project-wrapper a:hover .desc p {
  transform: translateY(1px);
  transition-delay: 0.3s;
  opacity: 1;
}
.work .project-wrapper a img {
  width: 100%;
  transition: all 0.3s;
}
.work .project-wrapper a .desc {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0,0.25);
  color: #fff;
  transition: all 0.3s;
}
.work .project-wrapper a .desc h2 {
  position: absolute;
  margin: 0 auto;
  transform: translateY(-50%);
  text-align: center;
  top: 50%;
  width: 100%;
  font-size: 28px;
  text-shadow: 2px 2px 16px #000;
  transition: all 0.3s;
}
.work .project-wrapper a .desc p {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s;
  opacity: 0;
  text-align: center;
  font-size: 18px;
  line-height: 25px;
}


